import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const tutorialSteps = [
  {
    label: 'Periodo de Gestacion',
    imgPath:
      'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRDoQBhc3KqZzn1bu3EApTKb3aX--jrngQ-ig&usqp=CAU',
  },
  {
    label: 'Periodo de 0 a 2 años',
    imgPath:
      'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSzqbNhb1Eb4-OpqThcEbYN1irjv6u5I_GTuA&usqp=CAU',
  },
  {
    label: 'Periodo de 2 a 4 años',
    imgPath:
      'https://i.pinimg.com/originals/9a/85/1d/9a851dd675d4d3b793fb8df4fb271b27.png',
  },
  {
    label: 'Periodo de 4 años y mas',
    imgPath:
      'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRLSiUBbcYrV6P86tDaHji3tAzMs7lpABUdtg&usqp=CAU',
  }
];

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: '100%'
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(4),
    backgroundColor: 'pink',
  },
  img: {
    height: 400,
    overflow: 'hidden',
    width: '50%',
  },
}));

function SwipeableTextMobileStepper() {
  const classes = useStyles();
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = tutorialSteps.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  return (
    <div className={classes.root}>
      <Paper square elevation={0} className={classes.header}>
        <Typography>{tutorialSteps[activeStep].label}</Typography>
      </Paper>
      <AutoPlaySwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {tutorialSteps.map((step, index) => (
          <div key={step.label}>
            {Math.abs(activeStep - index) <= 2 ? (
              <img className={classes.img} src={step.imgPath} alt={step.label} />
            ) : null}
          </div>
        ))}
      </AutoPlaySwipeableViews>
      <MobileStepper
        steps={maxSteps}
        position="static"
        variant="text"
        activeStep={activeStep}
        nextButton={
          <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
            Next
            {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Back
          </Button>
        }
      />
    </div>
  );
}

export default SwipeableTextMobileStepper;